import { KNN, shuffleArray } from '../../../../lib';
import { Solution as Renderer, renderPoints } from './renderer';
import { } from 'njscore';

export class Solution {
    public divHost: HTMLElement;
    constructor(id: string) {
        this.divHost = document.getElementById(id);


        // this.knnEx();
        this.irisPrediction();
    }
    public knnEx() {
        const XData = [[0, 0], [0, 1], [1, 2], [1, 3]]
        const yData = [0, 0, 1, 1]

        const val = [[1, 3]]

        const knn = new KNN(XData, yData);
        const result = knn.predict(val);

        console.log(result);
    }
    public irisPrediction() {
        const container = document.createElement('div');
        this.divHost.appendChild(container);
        container.id = 'container-knn'
        container.style.alignItems = 'center';
        container.style.marginTop = '50px';
        container.style.marginLeft = 'auto';
        container.style.marginRight = 'auto';
        container.style.width = '600px';
        container.style.height = '600px';
        const renderer = new Renderer(container.id);
        renderer.renderer.domElement.style.position = 'unset';
        const data = getIrisData();

        const pos0 = [];
        const pos1 = [];
        const pos2 = [];

        const col0 = [];
        const col1 = [];
        const col2 = [];

        let x = 0
        let y = 0
        let z = 0

        for (let i = 0; i < data.length; ++i) {
            const d = data[i]

            x+= d[0];
            y+= d[1];
            z+= d[2];

            if (d[4] === 0) {
                pos0.push(d[0], d[1], d[2]);
                col0.push(1, 0, 0);
            } else if (d[4] === 1) {
                pos1.push(d[0], d[1], d[2]);
                col1.push(0, 1, 0);
            } else if (d[4] === 2) {
                pos2.push(d[0], d[1], d[2]);
                col2.push(0, 0, 1);
            }
        }

        renderer.controls.target.set(x / data.length, y / data.length, z / data.length)
        renderPoints(renderer.scene, pos0, col0);
        renderPoints(renderer.scene, pos1, col1);
        renderPoints(renderer.scene, pos2, col2);




        shuffleArray(data);

        const inputs = [];
        const labels = [];
        for (let i = 0; i < data.length; ++i) {
            let row = [];
            for (let j = 0; j < data[i].length - 1; ++j) {
                row.push(data[i][j]);
            }
            inputs.push(row);
            labels.push(data[i][data[i].length - 1])
        }

        //     console.log('Iris data');
        //     console.log(inputs)

        const numTraining = 0.6;

        const train_inputs_data = inputs.splice(0, Math.floor(inputs.length * numTraining));
        const train_labels_data = labels.splice(0, Math.floor(labels.length * numTraining));

        const val_inputs_data = inputs;
        const val_labels_data = labels;

        //     console.log('train data inputs');
        //     console.log(train_inputs_data);
        //     console.log('train data labels');
        //     console.log(train_labels_data);

        //     console.log('validation data inputs');
        //     console.log(val_inputs_data);
        //     console.log('validation data labels');
        //     console.log(val_labels_data);

        const knn = new KNN(train_inputs_data, train_labels_data);
        const result = knn.predict(val_inputs_data);

        // console.log('Prediction :', result);
        // console.log('Ground truth ', val_labels_data);

        let failure = [];
        for (let i = 0; i < val_labels_data.length; ++i) {
            if (val_labels_data[i] !== result[i]) {
                failure.push(i);
            }
        }
        // console.log(String((100 - (failure.length / val_labels_data.length * 100)).toFixed(2)) + '% accuracy');
        // console.log('index:', failure);

        const div = document.createElement('div');
        let p = document.createElement('p');
        div.appendChild(p);
        p.textContent = String((100 - (failure.length / val_labels_data.length * 100)).toFixed(2)) + '% accuracy by KNN';

        p = document.createElement('p');
        div.appendChild(p);
        p.textContent = 'Ground truth -- ' + (val_labels_data as number[] ).splice(0, 30);

        p = document.createElement('p');
        div.appendChild(p);
        p.textContent = 'Prediction ------ ' + (result as number[] ).splice(0, 30);

        p = document.createElement('p');
        div.appendChild(p);
        p.innerHTML = '<a href="https://www.kaggle.com/search?q=iris" target="_blank">https://www.kaggle.com/search?q=iris</a>';

        

        container.appendChild(div);

    }
    public destroy() {
        // this.renderer.destroy();
        try {
            while (this.divHost !== undefined && this.divHost.lastElementChild !== undefined) {
                this.divHost.removeChild(this.divHost.lastElementChild);
            }
        } catch (error) { }
    }
}



